.tower {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 200px;
  width: 120px;
}

.tower-label {
  font-weight: bold;
  margin-bottom: 10px;
}

.peg {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: flex-end;
  height: 200px;
  width: 10px;
  background-color: #ccc;
  position: relative;
  margin-top: 10px;
}

.disk {
  height: 20px;
  border-radius: 4px;
  margin: 4px 0;
  transition: all 0.4s ease-in-out;
}

.disk-1 {
  width: 40px;
  background-color: #4CAF50;
}

.disk-2 {
  width: 60px;
  background-color: #2196F3;
}

.disk-3 {
  width: 80px;
  background-color: #FF9800;
}

.disk-4 {
  width: 100px;
  background-color: #9C27B0;
}
